var WKT = require('terraformer-wkt-parser');

var spaceStyle = {
    weight: 2,
    color: '#f00',
    opacity: 0.9,
    fillColor: '#f00',
    fillOpacity: 0
};
export default {
    data() {
        return {
            wkt: 'POLYGON ((112.346191 27.819731, 112.346191 31.508435, 119.069824 31.508435, 119.069824 27.819731, 112.346191 27.819731))',
            geoJson: `{
    "type": "Polygon",
    "coordinates": [
        [
            [112.346191, 27.819731],
            [112.946191, 32.508435],
            [119.969824, 32.508435],
            [119.069824, 27.819731],
            [112.346191, 27.819731]
        ]
    ]
}`,
            drawShapeMore: false,
            defaultStyle: `{
    "weight": 2,
    "color": "#f00",
    "opacity": 0.9,
    "fillColor": "#f00",
    "fillOpacity": 0
}`
        }
    },
    computed: {
        spaceStyle() {
            let str = this.defaultStyle;
            str = str.replace(/\s*/g, "");
            str = JSON.parse(str);
            return str;
        }
    },
    methods: {
        handleSetStyle() {
            let editLayer = this.$refs.editLayer;
            editLayer.setDrawStyle(this.spaceStyle);
            this.$message({ message: '颜色设置成功', type: 'success' });
        },
        handleChange() {
            this.drawShapeMore = !this.drawShapeMore;
        },
        // 绘制矩形  type 值为'rectangle', 'polygon'
        handleDrawShape(type, isSigle, isMeasure = false) {
            let editLayer = this.$refs.editLayer;
            if (isSigle) {
                editLayer.clearEditLayers();
            }
            editLayer.setDrawShapeMore(!isSigle);
            editLayer.setEditType(type, isMeasure);
            editLayer.setEditable(true);
            return false;
        },
        handleClearDraw(isClear) {
            let editLayer = this.$refs.editLayer;
            if (isClear != false) {
                editLayer.clearEditLayers();
            }
            editLayer.setEditable(false);
        },
        // 设置区域
        handleSetShape(type) {
            let gwJson = '';
            if (type === 'wkt') {
                gwJson = WKT.parse(this.wkt);
            } else {
                let str = this.geoJson;
                str = str.replace(/\s*/g, "");
                gwJson = JSON.parse(str);
                gwJson = {
                    "type": "Feature",
                    "properties": {},
                    "geometry": gwJson
                };
            }
            let editLayer = this.$refs.editLayer;
            let geojsonLayer = editLayer.setGeojsonParams(gwJson, {
                style: this.spaceStyle
            });
            editLayer.fitBounds(geojsonLayer);
        }
    }
}